body, h1, h2, h3, h4, h5, p, .btn {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
    font-family: sans-serif;
}

.btn {
    border: 0;
    outline: none;
    background: none;
    cursor: pointer;
    transition: .15s linear;
    border: 1px solid #266fbb;
    position: relative;
    color: #222;
    &:hover {
        color: #fff;
    }
    &:hover span {
        width: 100%;
    }
    span {
        width: 0;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #266fbb;
        z-index: -1;
        transition: .15s linear;
    }
}

.container {
    width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h1 {
        margin: 25px 0;
        text-align: center;
        color: #222;
    }
    .btn-top {
        width: 80%;
        height: 45px;
        font-size: 16px;
        &:hover {
            box-shadow: 0 0 4px #ccc;
        }
    }
    p {
        color: #222;
        margin: 35px 0;
    }
    img {
        width: 100%;
        margin-top: 50px;
    }
}

.btn-undo {
    width: 40px;
    height: 40px;
    border: none;
    box-shadow: 0 0 4px #ccc;
    border-radius: 50%;
    font-size: 22px;
    color: #266fbb;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 25px;
    transition: .15s linear;
    &:hover {
        box-shadow: 0 0 8px #ccc;
        color: #215ea0;
    }
}

.items {
    width: 1200px;
    margin: 25px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.item {
    width: 250px;
    height: 380px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 25px 15px;
    box-shadow: 0 0 4px #ccc;
    margin: 15px;
    img {
        height: 100px;
        box-shadow: 0 0 2px #ccc;
    }
    h3 {
        font-weight: bold;
        color: #222;
        font-size: 24px;
        height: 65px;
        display: flex;
        align-items: center;
        text-align: center;
    }
    h5 {
        font-size: 16.5px;
        color: #454545;
        font-weight: normal;
    }
    .btn-item {
        width: 100%;
        height: 45px;
    }
}

.page {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000000dd;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main {
    width: 600px;
    height: 625px;
    background-color: #fff;
    position: relative;
}

.main-top {
    width: 100%;
    height: 80px;
    padding: 0 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    h3 {
        font-size: 26px;
        color: #222;
    }
}

.btn-close {
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
    font-size: 18px;
    padding: 0;
    margin: 0;
    color: #222;
}

.info {
    padding: 25px 25px 0;
    p {
        padding: 10px 0;
        margin-bottom: 15px;
        color: #222;
        border-bottom: 1px solid #ccc;
        span {
            font-weight: bold;
        }
    }
}

.footer {
    width: 100%;
    height: 80px;
    padding: 0 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #222;
    p {
        font-size: 14px;
    }
    span {
        font-weight: bold;
    }
}

.loading {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #454545;
    font-size: 18px;
}

@media (max-height: 725px) {
    .main {
        height: 100%;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}

@media (max-width: 1200px) {
    .container, .items {
        width: 100%;
    }
}

@media (max-width: 600px) {
    .main {
        width: 100%;
    }
}